.popup__item {
    border: 0;
    box-sizing: border-box;
    font-style: normal;
    font-weight: normal;
    font-size: 14px;
    line-height: 17px;
    color: #000000;
    width: 100%;
    border-bottom: 1px solid rgba(0,0,0, 0.2);
  }
.popup__item:focus {
    outline: none;
}
.popup__item::placeholder {
  opacity: 0.5;
}